@import '../styles/variables';

header {
    background-color: $primary;
    padding: 1rem;
}

nav {
    display: flex;
    justify-content: center;
}

ul {
    display: flex;
    list-style-type: none;
    padding: 0;
    margin: 0;
}

li {
    margin: 0 1rem;
}

a {
    font-size: 1rem;
    color: $text-light;
    text-decoration: none;
    transition: color 0.3s ease;

    &:hover,
    &:focus {
        color: $accent;
    }
}
